import { CButton, CCard, CCardBody, CCol, CForm, CFormCheck, CFormInput, CFormSelect, CFormText, CFormTextarea, CInputGroupText, CModal, CModalBody, CModalFooter, CModalHeader, CModalTitle, CNav, CNavItem, CNavLink, CRow, CTabContent, CTabPane, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';
import React, { useEffect } from 'react'
import noImage from '../../assets/images/no-img.jpg';

import { EditorState, convertToRaw} from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { useState } from 'react';
import CIcon from '@coreui/icons-react';
import { cilPencil, cilPlus, cilTrash, cilX } from '@coreui/icons';
import "./product.scss";
import { ToastContainer, toast } from 'react-toastify';
import { json, useNavigate } from 'react-router-dom';
import { CustomTabs } from 'src/components';



const CategoryCreate = () => {
  const Navigate =  useNavigate();
  const [selectedImage, setSelectedImage] = useState({});
  const [gallery, setSelectedGallery] = useState(null);
  const [productVarition, setProductVarition] = useState(false);
  const [attr, setAttr] = useState([]);
  const [sprops, setProps] = useState('');
  const [svalue, setValue] = useState('');
  const [brands, setBrands]= useState([]);
  const [category, setCateogy]= useState([]);
  const [error, setError] = useState({});
  const [images, setImages] = useState([]);
  const [totalProduct, setTotalProduct] = useState([]);
  const [fProduct, setFProduct] = useState([]);
  const [selectedProd, setSelectedProd] = useState([]);  

const handelSearch = (e) =>{
  const filteredProduct =  totalProduct.filter((i)=>i.product_name.match('^'+e.target.value, "I"));
  setFProduct(filteredProduct);
  setProdVisible(true)
}


const handleSelectProd = (e) => {   
    setSelectedProd((prevSelectedProd) => [...prevSelectedProd, e]);
    setTotalProduct(prevSelected =>prevSelected.filter(item => item._id !== e._id));
    setProdVisible(false)
}

const [prodVisible, setProdVisible] = useState(true);
const handleDeleteProd = (e) => { 
  setTotalProduct((prevSelectedProd) => [...prevSelectedProd, e]);
  setSelectedProd(prevSelected =>prevSelected.filter(item => item._id !== e._id));
};


useEffect(() => {  
  let url =  process.env.REACT_APP_API_URL+"product/list"
  fetch(url, {
    method: 'POST',
    redirect: 'follow'
  })
  .then(response => response.json())
  .then(result => {setTotalProduct(result.data)})
  .catch(error => console.log('error', error));

}, []);



  


  
  const handleImageChange2 = (event) => {
    const files = event.target.files;
  
    if (files.length > 0) {
      const filePromises = Array.from(files).map((file) => {
        // Create a separate FormData for each file
        const formData = new FormData();
        formData.append("image", file, file.name);
        formData.append("type", "image");
  
        const requestOptions = {
          method: 'POST',
          body: formData,
          redirect: 'follow'
        };
  
        // Make separate API calls for each file
        return fetch(process.env.REACT_APP_API_URL + "images/add", requestOptions)
          .then(response => response.json())
          .catch(error => {
            console.log('error', error);
            return null; // Return null if an error occurs during the API call
          });
      });
  
      // Wait for all API calls to complete using Promise.all
      Promise.all(filePromises).then((responses) => {
        // Filter out any null responses (which occurred due to errors)
        const validResponses = responses.filter(response => response !== null).map(i=>i.data);
        // Do whatever you want to do with the validResponses, e.g., set it to state
        console.log(validResponses);
        setSelectedGallery(validResponses);
      });
    } else {
      setSelectedGallery([]);
    }
  };
  
  

  const [editorState, setEditorState] = useState(
    () => EditorState.createEmpty(), 
  );
const handelSubmit = (e)=>{
    e.preventDefault();
    let rawContentState = convertToRaw(editorState.getCurrentContent());
    let contentInPlainText = JSON.stringify(rawContentState);
   
    const [productName, productSku, productBrand, productCategory, shortDescription, status, s, g, b, b2, form_check] = e.target;
    let hasError  = false;
    const error  = {};
    console.log(form_check.value);
    const data = {
      product_name: productName.value,
      sku: productSku.value,
      short_description: shortDescription.value,
      long_description: contentInPlainText,    
      brand:productBrand.value,
      category: productCategory.value,
      status: parseInt(status.value),
      photo: selectedImage._id,
      photo_gallery: gallery ? gallery.map(i=>i._id) : [],
      relatedProducts: selectedProd ? selectedProd.map(i=>i._id) : [],
    }
    console.log(JSON.stringify(data));

    if(!data.product_name){
      error['product-name'] = "Please add a product Name";
      hasError = true;
    }
    if(!data.short_description){
      error["short_description"]="Please add Short description";
      hasError = true;
    }   
    if(!data.brand){
      error['product-brand'] = "Please select a Brand Name";
      hasError = true;
    }
    if(!data.category){
      error["product-category"]="Please select product category";
      hasError = true;
    }

    setError(error)



   if(!hasError){
    fetch(process.env.REACT_APP_API_URL+"product/create", {
      method: 'POST',
      headers:{
        "Content-Type": "application/json"
      },
      body: JSON.stringify(data),
      redirect: 'follow'
    })
    .then(response => response.json())
    .then(result => {
      if(result.status==200){
        toast.success(result.message);
        setTimeout(()=>{
          Navigate("/products")
        }, 3000)
      }
    })
    .catch(error => console.log('error', error));
  }

  }


  const handelVarients = (e)=>{
    e.preventDefault();
    console.log(e.target);

  }
  
  const handleAdd = () => {
    if (sprops && svalue) {
      setAttr((prev) => [...prev, { sProp: sprops, sValue: svalue }]);
      // Clear the input fields after adding the attribute
      setProps('');
      setValue('');
    } else {
      toast.error('Please enter empty fields.');
    }
  };
  


  useEffect(()=>{ 
      let url =  process.env.REACT_APP_API_URL+"brand/list"
      fetch(url, {
        method: 'POST',
        redirect: 'follow'
      })
      .then(response => response.json())
      .then(result => {setBrands(result.data) })
      .catch(error => console.log('error', error));

      let url2 =  process.env.REACT_APP_API_URL+"category/list"
      fetch(url2, {
        method: 'POST',
        redirect: 'follow'
      })
        .then(response => response.json())
        .then(result => {setCateogy(result.data) })
        .catch(error => console.log('error', error));
  }, [])






 

  const [selectedImages, setSelectedImages] = useState([]);

  const handleImageSelect = (imageId) => {
    if (selectedImages.includes(imageId)) {
      setSelectedImages(prevSelectedImages =>
        prevSelectedImages.filter(id => id !== imageId)
      );
    } else {
      setSelectedImages(prevSelectedImages => [...prevSelectedImages, imageId]);
    }
  };

  // Handle image upload here
  const handleImageUpload = (fileInput) => {
    var formdata = new FormData();
    formdata.append("image", fileInput.files[0], fileInput.files[0].name);
    formdata.append("type", "image");

    var requestOptions = {
      method: 'POST',
      body: formdata,
      redirect: 'follow'
    };

    fetch(process.env.REACT_APP_API_URL + "images/add", requestOptions)
      .then(response => response.json())
      .then(result => {console.log(result); setSelectedImage(result.data)})
      .catch(error => console.log('error', error));
  };

  useEffect(() => {
    let url = process.env.REACT_APP_API_URL + "images/list";
    fetch(url, {
      method: 'POST',
      redirect: 'follow'
    })
      .then(response => response.json())
      .then(result => {
        setImages(result.data);
        // Update the tabsData with the latest images
        setTabsData(prevTabsData => {
          const updatedTabsData = prevTabsData.map(tab => {
            if (tab.id === 2) {
              return {
                ...tab,
                content: (
                  <div>
                    <CRow>
                      {result.data.map((item, key) => (
                        <CCol key={key} lg={2}>
                          <img src={process.env.REACT_APP_BASE_URL+item.image} alt={`Image ${key}`}  style={{maxWidth:"100%"}}/>
                          <label>
                          <input
                            type="checkbox"                            
                          />
                          Select
                        </label>
                        </CCol>
                      ))}
                    </CRow>
                  </div>
                )
              };
            }
            return tab;
          });
          return updatedTabsData;
        });
      })
      .catch(error => console.log('error', error));
  }, []);

  const [tabsData, setTabsData] = useState([
    {
      id: 1,
      label: 'Upload files',
      content: (
        <div>
          <h3>Drop files to upload</h3>
          <p>or</p>          
          <CButton>Select Files</CButton>
        </div>
      ),
    },
    {
      id: 2,
      label: 'Media Library',
      content: (
        <div>
          <CRow></CRow>          
        </div>
      ),
    }
  ]);

      
  return (
    <>  
    <ToastContainer />
    <h1 className='headingType1 text-center mb-4'>Add New Product</h1>
      <CCard >            
          <CCardBody className='p-5'>
              
              <CForm onSubmit={handelSubmit}>
                <CRow className='g-4'>
                  <CCol lg={8}>   
                    <CRow>
                      <CCol lg={6} className='mb-4'>
                        <CFormInput placeholder='Product Name' label="Product Name" name='product-name'/>
                        <div className='hasError'>{error['product-name']}</div>
                      </CCol>
                      <CCol lg={6} className='mb-4'>                
                        <CFormInput placeholder='Sku' label="Sku" name='product-sku'/>                        
                      </CCol>
                      <CCol lg={4} className='mb-4'>             
                          <CFormSelect name='product-brand' label="Select Brand">
                            <option value={""}>Select Brand</option>
                            {brands.map((item, key)=>(
                              <option key={key} value={item._id}>{item.brand_name}</option>
                            ))}                           
                          </CFormSelect>
                          <div className='hasError'>{error["product-brand"]}</div>
                      </CCol>
                      <CCol lg={4} className='mb-4'>                  
                          <CFormSelect name='product-category' label="Select Category">
                            <option value={""}>Select Category</option>
                            {category.map((item, key)=>(
                              <option key={key} value={item._id}>{item.name}</option>
                            ))}                            
                          </CFormSelect>
                          <div className='hasError'>{error["product-category"]}</div>
                      </CCol>
                      <CCol lg={4} className='mb-4'>   
                          {/* <label>Select Category</label>                     
                          <CFormSelect>
                            <option>Select Category</option>
                          </CFormSelect> */}
                      </CCol>
                      <CCol lg={12} className='mb-4'>                
                        <CFormTextarea placeholder='Short Description' label="Short Description" name='short-description'/>
                        <div className='hasError'>{error["short_description"]}</div>
                      </CCol>
                      <CCol lg={12} className='mb-4'> 
                        <label className='form-label'>Long Description</label>
                        <Editor
                          editorState={editorState}
                          onEditorStateChange={setEditorState}
                        />   
                      </CCol>
                      {/* <CCol lg={6} className='mb-4'>                 
                          <CFormInput type="file" label="Video" name='video'/>
                      </CCol> */}
                      <CCol lg={6} className='mb-4'>                                       
                        <CFormSelect name='status' label="Status">
                          <option value={"1"}>Active</option>
                          <option value={"0"}>Inactive</option>
                        </CFormSelect>
                      </CCol>
                      <CCol lg={12} className='mb-4'>
                        <h5 className='mb-3 '>Specifications</h5>
                        <CTable bordered > 
                          <CTableHead>
                            <CTableRow>
                              <CTableHeaderCell>
                                Property
                              </CTableHeaderCell>
                              <CTableHeaderCell>
                                Value
                              </CTableHeaderCell>
                              <CTableHeaderCell>
                                Edit
                              </CTableHeaderCell>
                            </CTableRow>
                          </CTableHead>    
                          <CTableBody>            
                            {attr.map((i, key)=>(                            
                                <CTableRow key={key}>
                                  <CTableDataCell lg="true">
                                    {i.sProp}
                                  </CTableDataCell>
                                  <CTableDataCell lg="true">
                                    {i.sValue}
                                  </CTableDataCell>
                                  <CTableDataCell lg={"auto"} width={50}>
                                    <div className='arInputWrapper'>
                                      <CIcon icon={cilPencil} width={16} />
                                    </div>
                                  </CTableDataCell>
                                </CTableRow>                           
                            ))}
                          </CTableBody>     
                        </CTable>

                      
                        
                        <div className='each_attr_Wrap'>
                          <CRow>
                            <CCol lg>
                              <CFormInput placeholder='Attribute Name' onChange={i=>setProps(i.target.value)} value={sprops}/>
                            </CCol>
                            <CCol lg>
                              <CFormInput placeholder='Attribute Value' onChange={i=>setValue(i.target.value)} value={svalue}/>
                            </CCol>
                            <CCol lg={"auto"}>
                              <CButton color='success' onClick={handleAdd}><CIcon icon={cilPlus} width={16} />Add Attribute</CButton>
                              {/* <CButton color='danger'><CIcon icon={cilTrash} width={16} /></CButton> */}
                            </CCol>
                          </CRow>
                        </div>
                        

                        <div className="mt-4">
                          <h5 className='mb-3 '>Related Product</h5>
                          <ul className='selectedProducts'>
                            {selectedProd.map((item,  key)=>(
                              <li  key={key}><span> {item.product_name} <CIcon icon={cilX}  onClick={()=>handleDeleteProd(item)}/></span></li>
                            ))}                            
                          </ul>
                          <div>
                              <CFormInput type='text' placeholder='Search Related Product...' onChange={handelSearch}/>
                              {prodVisible&& 
                              <ul className='searchReference'>
                                {fProduct.map((item, key)=>(
                                  <li key={key} onClick={()=>handleSelectProd(item)}>
                                    <label>{item.product_name}</label>                                    
                                  </li>                                  
                                ))}
                              </ul>}
                          </div>
                        </div>

                      </CCol> 
                      <CCol lg={12} className='mb-4'>
                       

                      </CCol>


                    </CRow> 
                  </CCol>
                  <CCol lg={4}>
                    <div className='mb-4'>
                      <h5>Product Image</h5>
                      
                      <div className='profileImage'>
                        <img src={selectedImage.image?process.env.REACT_APP_BASE_URL+selectedImage.image:noImage} width={'100%'} className='mb-3'/> 
                        <CFormInput type='file' onChange={(i)=>handleImageUpload(i.target)}/> 
                      </div>
                      <CFormText component="span" id="exampleFormControlInputHelpInline">
                      Product Image must be 1200px X 12000px and ( 72dpi if possible) 
                      </CFormText>
                      

                      {/* <CFormInput type='file' onChange={handleImageChange} name='profileImage' /> */}
                    </div>
                    <h5>Product Thumnails</h5>
                    <ul className='arGalleryImg'> 
                      {gallery?.map((item, index) => (
                        <li key={index}>
                          <img  src={process.env.REACT_APP_BASE_URL+item.image} alt={item.name} />
                        </li>
                      ))}
                      <li>
                        <label htmlFor='myGallery' className='addGallery'>
                          <CIcon icon={cilPlus}  />
                        </label>
                        <CFormInput id='myGallery' multiple={true} type='file' onChange={handleImageChange2} name='myGallery' />
                      </li>
                    </ul>
                    <CFormText component="span" id="exampleFormControlInputHelpInline">
                      Product Thumnails must be 1200px X 12000px and ( 72dpi if possible) 
                    </CFormText>
                  </CCol>
                  <CCol lg={12}  className='text-center'>
                    <CButton color='primary' className='py-2 px-5' type='submit'>Submit</CButton>
                  </CCol>
                </CRow>
              </CForm>
          </CCardBody>
      </CCard>
      
    </>
  )
}

export default CategoryCreate;
